.inputAndSelect {
    display: flex;
    flex-direction: row;
}

@media screen and (max-width: 930px) {
    .inputAndSelect {
        flex-wrap: wrap;
    }
}

.buttonsContainer {
    z-index: 2;
    display: flex;
    position: relative;
    left: 553px;
}

@media screen and (max-width: 880px) {
    .buttonsContainer {
        left: 93%;
    }
}

@media screen and (max-width: 740px) {
    .buttonsContainer {
        left: 90%;
    }
}

@media screen and (max-width: 510px) {
    .buttonsContainer {
        left: 87%;
    }
}


.button {
    width: 2rem;
    font-family: inherit;
    padding: 5px;
    height: 2rem;
    border-radius: 0;
    /*color: #656666*/
    border: 1px solid #c3c3c3;
    background: white;
    outline: none;
    font-size: 1.4rem;
    display: flex;
    justify-content: center;
    align-items: center;
}