.languageSkills {
    display: flex;
    flex-direction: row;
    align-items: center;
    position: relative;
    padding-bottom: 35px;
}

.button {
    width: 2rem;
    height: 2rem;
    font-family: inherit;
    padding: 5px;
    border-radius: 0;
    /*color: #656666*/
    border: 1px solid #c3c3c3;
    background: white;
    outline: none;
    font-size: 1.4rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.buttonsContainer {
    z-index: 2;
    display: flex;
    position: absolute;
    top: 70px;
    left: 90%;
}